import React from 'react';
import { withRouter } from 'react-router-dom'

import './style.css';

function Header(props) {
  const { title, history, children, btnBackActive } = props;

  const goBack = () => history.goBack();

  return(
    <div className='header' >
      { btnBackActive && <div className='backStep' onClick={goBack}/> }
      {children || <h1 className='headerTitle'>{title}</h1>}
    </div>
  )
}

export default withRouter(Header);
