import React, { Fragment } from 'react';
import { Switch, Route } from 'react-router-dom'

import FirstPreloader from '../firstPreloader';
import Dishes from '../Dishes';
import OnBoarding from '../onboarding';
import InfoUser from '../infoUser';
import Goals from '../goals';
import NutritionistLoader from '../nutritionist';
import CartLoader from '../cartLoader';
import Shop from '../shop'

function Roaster({ activeGoal, setActiveGoal, setAllDishes, allDishes}) {
  return (
    <Fragment>
      <Route exact path='/' component={FirstPreloader}/>
      <Route exact path='/dishes' render={() => (<Dishes activeGoal={activeGoal} setIdDishes={setAllDishes}/>)}/>
      <Route exact path='/Shop' render={() => (<Shop allDishes={allDishes}/>)}/>
      <Route exact path='/profile' />
      <Route exect path='/addingProduct' component={CartLoader}/>
      <Route exect path='/nutri' component={NutritionistLoader}/>
      <Route exact path='/goals' render={() => (<Goals setActiveGoal={setActiveGoal}/>)}/>
      <Route exact path='/info-user' component={InfoUser}/>
      <Route exact path='/onboarding' component={OnBoarding}/>
    </Fragment>
  )
}

export default Roaster;
