import React, { Fragment, useState, useEffect } from 'react';
import axios from 'axios';

import Header from '../layout/header';
import Footer from '../layout/footer';
import Button from '../button';
import WrapperContent from '../wrapperContent';

import { BASE_URL } from '../../constant';


import './style.css';

function Goals(props) {
  const [allGoals, setAllGoals] = useState([]);

  useEffect(() => {
    axios(`${BASE_URL}/api/v1/goals`)
      .then(res => setAllGoals(res.data))
  }, [])

  return(
    <Fragment>
      <div className='goals'>
        <Header  title='Ваша цель?' btnBackActive/>
        <WrapperContent className={'goalsBlock'}>
          {
            allGoals.map(item => {
              return (
                <div
                  key={item.slug}
                  className='itemGoal'
                  onClick={() => props.setActiveGoal(item.slug)}
                >
                  <img
                    src={require(`./icon/${item.slug}.png`)}
                    className='logoGoal'
                  />
                  <div className='discription'>{item.title}</div>
                </div>
              )
            })
          }
          <Button link='nutri' text='Подобрать блюда'/>
        </WrapperContent>
      </div>
      <Footer />
    </Fragment>
  )
}

export default Goals;
