import React from 'react';
import { withRouter } from 'react-router-dom'

import cartGroup from './icon/cartGroup.png';

import './style.css';

function CartLoader (props) {
  const { history } = props;
  setTimeout(() => {
    history.push('shop')
  }, 3000)

  return (
    <div className='cartLoader '>
      <div className='wrapperLogo'>
        <img src={cartGroup} className='logo'/>
      </div>
    <div className='titleLoader'>Ищем лучшие цены <br /> для вашей корзины</div>
    </div>)
}

export default withRouter(CartLoader);


