import React, { Fragment } from 'react';

import Header from '../layout/header';
import WrapperContent from '../wrapperContent';
import Button from '../button';

import iconPrice from './icon/price.svg';

import './style.css'

function Cart(props) {
  const { isOpen, products, changeIsOpen } = props;

  return (
    <Fragment>
      {
        isOpen && (
        <div className='cart'>
          <Header>
            <div>
              <span className='arrowBack' onClick={() => {
                changeIsOpen(false)
              }}/>
              <h1 className='headerTitle'>Ваша корзина</h1>
            </div>
          </Header>
          <WrapperContent className=''>
            {
              products.map(item => {
                return (
                  <div className='product'>
                    <img src={item.image} className='preview'/>
                    <div className='discription'>
                      <div className='title'>{item.title}</div>
                      <div className='infoProduct'>
                        <img src={iconPrice} className='iconPrice'/>
                        <div className='priceProduct'>{item.price} руб</div>
                        <div className='changeCountProduct '>- 1 +</div>
                      </div>
                    </div>
                  </div>
                )
              }
            )}
          </WrapperContent>
          <Button text='Заказать'/>
        </div>
        )
      }
    </Fragment>
  )
}

export default Cart;
