import React, { Fragment, useState, useEffect } from 'react';
import axios from 'axios';

import Header from '../layout/header';
import Footer from '../layout/footer';
import Button from '../button';
import WrapperContent from '../wrapperContent';
import StructureDish from '../structureDish';

import iconPrice from './icon/price.svg';
import iconTwoPerson from './icon/twoPerson.svg'
import iconUpdateDish from './icon/updateDish.svg'


import { BASE_URL } from '../../constant';


import './style.css';

function Dishes(props) {
  const [allDishes, setAllDishes] = useState([]);
  const [countDay, setCoundDay] = useState(1);
  const [isOpen, changeIsOpen] = useState(false);
  const [idProduct, changeIdProduct] = useState(0);

  const { activeGoal, setIdDishes } = props;

  useEffect(() => {
    axios(`${BASE_URL}/api/v1/dishes?goal_slug=${activeGoal}&days=${countDay}`)
      .then(res => setAllDishes(res.data))
  }, [countDay]);

  const idDishes = [];

  const getDish = (arr) => {
    return arr.map(item => {

      idDishes.push(item.id);

      return (
        <div key={item.id} className='dish' onClick={
          () => {
            changeIsOpen(true)
            changeIdProduct(item.id)
          }
        }>
          <img src={item.image_url} className='previewDish' />
          <div className='discription'>
            <div className='title'>{item.title}</div>
            <div className='infoDish'>
              <img src={iconPrice}/>
              <div className='price'>{item.price} руб</div>
              <img src={iconTwoPerson} />
              <div className='updateDish'>
                <img src={iconUpdateDish}/>
              </div>
            </div>
          </div>
        </div>
      )
    });
  }

  return(
    <Fragment>
      {
      !isOpen && (
        <div className='dishes'>
          <Header>
            <div className='changeDay' onClick={() => setCoundDay(1)}><span>1 день</span></div>
            <div className='changeDay' onClick={() => setCoundDay(2)}><span>2 дня</span></div>
            <div className='changeDay' onClick={() => setCoundDay(3)}><span>3 дня</span></div>
            <div className='changeAllDishes' onClick={() => {
              setAllDishes([])
              setCoundDay(countDay)
            }}><span>Обновить список блюд</span></div>
          </Header>
          <WrapperContent >
            {
              allDishes.map(item => {
                return (
                  <div key={`day-${item.day}`}>
                    <div className='day'>День {item.day}</div>
                    {getDish(item.dishes)}
                  </div>
                )
              })
            }
            <Button link='addingProduct' text='Сравнить цены'/>
          </WrapperContent>
        </div>
      )}
      <StructureDish
        idProduct={idProduct}
        isOpen={isOpen}
        changeIsOpen={changeIsOpen}
      />
      <Footer />
    </Fragment>
  )
}

export default Dishes;
