import React, { Fragment, useState, useEffect } from 'react';
import axios from 'axios';

import Header from '../layout/header';
import Footer from '../layout/footer';
import WrapperContent from '../wrapperContent';
import Cart from '../cart';

import cart from './icon/cart.svg';
import lock from './icon/lock.svg';

import { BASE_URL } from '../../constant';


import './style.css';

function Shop(props) {
  const [allShops, setAllShops] = useState([]);
  const [cartProduct, setCartProduct] = useState([])
  const [isOpen, changeIsOpen] = useState(false);

  useEffect(() => {
    const diactiveShops = [
      {shop_name: "Ашан", slug: "auchan", price: 'XXX', products: undefined},
      {shop_name: "Пятерочка", slug: "roundabout", price: 'XXX', products: undefined},
      {shop_name: "Азбука вкуса", slug: "alphabet", price: 'XXX', products: undefined}
    ]

    axios(`${BASE_URL}/api/v1/carts?dish_ids[]=83&dish_ids[]=84&dish_ids[]=85`)
      .then(res => setAllShops([...res.data, ...diactiveShops]))
  }, [])

  return(
    <Fragment>
      {!isOpen && (
        <div className=''>
          <Header  title='Цена в торговой сети' />
          <WrapperContent className='shopsBlock'>
            {allShops.map(item => {
                return (
                  <div
                    key={item.slug}
                    className='itemShop'
                    onClick={() => {

                      item.products && changeIsOpen(true)
                      setCartProduct(item.products)
                    }}
                  >
                    <img
                      src={require(`./icon/${item.slug}.png`)}
                      className='logoShop'
                    />
                    <div className='shopInfo'>
                      <div className='discription'>{item.price} РУБ</div>
                      {item.products ? (
                      <div className='checkShopt buttonShop'><img src={cart}/>К списку<br /> продуктов</div>
                      ) : (<div className='disableShop buttonShop'> <img src={lock}/>Оформить <br /> подписку</div>)}
                    </div>
                  </div>
                )
              })
            }
          </WrapperContent>
        </div>
      )}
      <Cart products={cartProduct} isOpen={isOpen} changeIsOpen={changeIsOpen}/>
      <Footer />
    </Fragment>
  )
}

export default Shop;
