import React from 'react'
import { Link } from 'react-router-dom'

import './style.css'

function Button(props) {
const { link, text } = props
  return (
    <div className={'button'} >
      <Link to={link} className='btnLink'>
        {text}
         <span className='arrow'/>
      </Link>
    </div>
  )
}

export default Button;
