import React from 'react';
import { withRouter } from 'react-router-dom'

import logo from './icon/first.png';
import carrot from './icon/carrot.svg';
import corn from './icon/corn.svg';
import cabbage from './icon/cabbage.svg';
import './style.css';

function FirstPreloader (props) {
  const { history } = props;
  setTimeout(() => {
    history.push('onboarding')
  }, 3000)
  return (
    <div className='preloader'>
      <div className='wrapperLogo'>
        <img src={logo} className='logo'/>
        <div>
          <img src={corn} className='corn produce'/>
        </div>
        <div>
          <img src={carrot} className='carrot produce'/>
        </div>
        <div>
          <img src={cabbage} className='cabbage produce'/>
        </div>
      </div>

    </div>)
}

export default withRouter(FirstPreloader);


