import React, { Fragment, useState, useEffect } from 'react';
import axios from 'axios';

import Header from '../layout/header';
import WrapperContent from '../wrapperContent';

import { BASE_URL } from '../../constant';

import './style.css'

function StructureDish(props) {
  const { isOpen, idProduct, changeIsOpen } = props;

  const [infoDish, setInfoDish] = useState([])

  useEffect(() => {
    axios(`${BASE_URL}/api/v1/dishes/${idProduct}`)
      .then(res => setInfoDish(res.data))
  }, [idProduct])

return (
  <Fragment>
    {
      isOpen && (
      <div className='structureDish'>
        <Header>
          <div className='previewStructureDish'>
            <span className='arrow' onClick={() => {
              changeIsOpen(false)
            }}/>
            <img src={infoDish.image_url} className='preview'/>
            <div className='title'>{infoDish.title}</div>
          </div>
        </Header>
        <WrapperContent className='infoIngridiant'>
          <div className='kbj'>
            <div className='blockInfo'>{infoDish.calories}<br /> калории</div>
            <div className='blockInfo'>{infoDish.carbohydrates}<br /> углеводы</div>
            <div className='blockInfo'>{infoDish.protein}<br /> белки</div>
            <div className='blockInfo'>{infoDish.fat}<br /> жиры</div>
          </div>
          <div className='listIngridiant'>
            <h3>Ингредиенты</h3>
            <ul>
              {
                infoDish.inrgedients &&  infoDish.inrgedients.map(item => {
                 return (
                    <li className='ingridiant'>
                      <div>{item.title}</div>
                      <div>{item.value}</div>
                    </li>
                  )
                })
              }
            </ul>
            <div>
              <h3>Пищевая ценность в 100г блюда</h3>
              <ul>
                {
                  infoDish.nutritional_values && infoDish.nutritional_values.map(item => {
                  return (
                      <li className='ingridiant'>
                        <div>{item.title}</div>
                        <div>{item.value}</div>
                      </li>
                    )
                  })
                }
              </ul>
            </div>

          </div>
        </WrapperContent>
      </div>
      )
    }
  </Fragment>
)
}

export default StructureDish;
