import React from 'react';
import { withRouter } from 'react-router-dom'

import carrot from './icon/carrot.svg';
import corn from './icon/corn.svg';
import cabbage from './icon/cabbage.svg';
import logoNutriLoader from './icon/logoNutriLoader.png';
import './style.css';

function NutritionistLoader (props) {
  const { history } = props;

  setTimeout(() => {
    history.push('dishes')
  }, 3000)

  return (
    <div className='preloader'>
      <div className='wrapperLogo'>
        <img src={logoNutriLoader} className='logoNutri'/>
        <div>
          <img src={corn} className='corn produce'/>
        </div>
        <div>
          <img src={carrot} className='carrot produce'/>
        </div>
        <div>
          <img src={cabbage} className='cabbage produce'/>
        </div>
      </div>
      <div className='discription'>Диетологи работают над составлением меню</div>
    </div>)
}

export default withRouter(NutritionistLoader);


